import { prisma } from '@/lib/prisma';
import { StarIcon } from '@heroicons/react/24/solid';

async function getReviews() {
  const reviews = await prisma.review.findMany({
    take: 20, // Limit to the 20 most recent reviews
    orderBy: {
      createdAt: 'desc',
    },
    include: {
      user: {
        select: { name: true },
      },
      product: {
        select: { name: true },
      },
      employee: {
        select: { name: true },
      },
    },
  });
  return reviews;
}

export async function ReviewsSlideshow() {
  const reviews = await getReviews();

  if (reviews.length === 0) {
    return null; // Don't render anything if there are no reviews
  }

  const extendedReviews = [...reviews, ...reviews];

  return (
    <div className="py-12 bg-gray-50 overflow-hidden">
      <div className="relative">
        <h2 className="text-3xl font-bold text-center mb-8 text-gray-800">
          What Our Customers Are Saying
        </h2>
        <div className="flex animate-marquee-slow hover:pause">
          {extendedReviews.map((review, index) => (
            <div key={`${review.id}-${index}`} className="flex-shrink-0 w-80 mx-4">
              <div className="bg-white p-6 rounded-lg shadow-md h-full">
                <div className="flex items-center mb-3">
                  <div className="flex">
                    {[...Array(5)].map((_, i) => (
                      <StarIcon
                        key={i}
                        className={`h-5 w-5 ${
                          i < review.rating ? 'text-yellow-400' : 'text-gray-300'
                        }`}
                      />
                    ))}
                  </div>
                </div>
                {review.comment && (
                  <p className="text-gray-600 italic mb-4">"{review.comment}"</p>
                )}
                <p className="text-sm font-semibold text-gray-700">
                  - {review.user?.name || 'Anonymous'} on {review.product?.name || review.employee?.name || 'our service'}
                </p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
} 